@font-face {
    font-family: EconomicaBold;
    src: url(assets/fonts/Economica-Bold.ttf);
}

@font-face {
    font-family: MontserratMedium;
    src: url(assets/fonts/Montserrat-Medium.ttf);
    font-weight: 800;
}

body {
    text-align: center;
    font-size: 20px;
    padding: 2.5%;
    font-family: 'MontserratMedium';
    background-color: rgb(252, 250, 250);
}

h1, h2, h3, h4, h5 {
    font-family: 'EconomicaBold';
}

p1 {
    display: block;
    margin-top: .5em;
    margin-bottom: 0em;
    margin-left: 0;
    margin-right: 0;
    font-size: 14px;
}

p2 {
    margin-top: 0em;
    font-size: 12px;
}

hr {
    background-color: lightgray !important;
    height: 1px;
}

.profile {
    width: 300px;
    height: 300px;
    border-radius: 150px;
}